.item {
   display: flex;
   justify-content: space-between;
   align-items: center;
   flex-direction: column;
   min-height: 25px;
   padding: 15px 0;
   font-style: italic;
   color: rgb(63, 40, 40);
}

@media screen and (min-width: 1280px) {
   .item {
      flex-direction: row;
      padding: 20px 0;
   }
}

.item:not(:last-child) {
   border-bottom: 1px solid rgb(119, 119, 119, .5);

}

.itemContact {
   display: flex;
   justify-content: space-between;
   width: 260px;
   margin-bottom: 15px;
}

@media screen and (min-width: 768px) {
   .itemContact {
      width: 300px;
   }
}

@media screen and (min-width: 1280px) {
   .itemContact {
      width: 320px;
      margin: 0;
   }
}

.itemText {
   overflow: hidden;
   padding: 0 3px;
}

.itemText:not(:last-child) {
   margin-right: 10px;
}

.btnWrapper {
   margin-left: auto;
}

.btn {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   margin: 0;
   border-radius: 50%;
   padding: 10px;
   cursor: pointer;
   border: none;
   font-family: 'Comfortaa', cursive;
   color: rgba(255, 255, 255, 0.5);
   font-size: 14px;
   box-shadow: inset 0px 3px 5px rgba(255, 255, 255, 0.5), 0px 0px 10px rgba(0, 0, 0, 0.15);
   background: rgb(2, 0, 36);
   background: linear-gradient(45deg, rgba(2, 0, 36, 0) 5%, rgba(255, 255, 255, .5) 6%, rgba(255, 255, 255, 0) 9%, rgba(255, 255, 255, .5) 10%, rgba(255, 255, 255, 0) 17%, rgba(255, 255, 255, .5) 19%, rgba(255, 255, 255, 0) 21%);
   background-size: 150%;
   background-position: right;
   transition: all .25s ease-in-out;
}

.btn:not(:last-child) {
   margin-right: 8px;
}

.btn:hover {
   background-position: left;
   color: white;
   box-shadow: inset 0px 3px 5px rgba(255, 255, 255, 1), 0px 0px 10px rgba(0, 0, 0, 0.25);
}

.btnLink {
   text-decoration: none;
   margin-right: 10px;
}